import { createContext, ReactNode, useContext, useMemo, useState } from "react";

import { BusinessTimeZoneInterface } from "Shared/Interfaces/Business/BusinessProfile";
import countriesData from "Constants/Data/countries.json";
import timeZonesData from "Constants/Data/time-zones.json";
import industriesData from "Constants/Data/industries.json";

import { SettingsApiContextState, SettingsDataContextState } from "./types";

const SettingsData = createContext<SettingsDataContextState | undefined>(
  undefined
);
const SettingsApi = createContext<SettingsApiContextState | undefined>(
  undefined
);

const SettingsProvider = ({ children }: { children: ReactNode }) => {
  const countriesList: any[] = countriesData;
  const [provinces, setProvinces] = useState<any[]>([]);
  const industryList: string[] = industriesData;
  const timeZoneList: BusinessTimeZoneInterface[] = timeZonesData;
  const data: SettingsDataContextState = {
    timeZoneList,
    industryList,
    countriesList,
    provinces,
  };
  const api = useMemo<SettingsApiContextState>(
    () => ({
      setProvinces,
    }),
    []
  );

  return (
    <SettingsApi.Provider value={api}>
      <SettingsData.Provider value={data}>{children}</SettingsData.Provider>
    </SettingsApi.Provider>
  );
};

export const useSettingsData = () => {
  const context = useContext(SettingsData);
  if (context === undefined) {
    throw Error(
      "Item must be used inside of SettingsProvider, otherwise it will not function correctly."
    );
  }
  return context;
};

export const useSettingsApi = () => {
  const context = useContext(SettingsApi);
  if (context === undefined) {
    throw Error(
      "Item must be used inside of SettingsProvider, otherwise it will not function correctly."
    );
  }
  return context;
};

export default SettingsProvider;
